html {
    overflow: hidden;
}

.game-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
}

.header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    top: 0;
    min-width: 100%;
    background-color: rgb(41, 41, 41);
    color: white;
}

.title-container,
.score-container {
    margin-left: 5em;
    margin-right: 5em;
}

.score-container {
    display: flex;
    flex-direction: column;
}

.current-score-container,
.best-score-container {
    display: flex;
    gap: 1em;
}

.score-tag {
    font-weight: bold;
}

.score-tag,
.score {
    font-size: 1.5rem;
    margin: 0;
    width: max-content;
}

.card-display {
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(6, 1fr);
    width: 100%;
    height: 100%;
}

.card {
    cursor: pointer;

}

.card:hover {
    border: solid 0.5em white;
}

.win-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    color: white;
    margin-top: 5%;
}

.play-again {
    cursor: pointer;
    border-radius: 3em;
    border: solid white;
    font-size: 1.5rem;
    width: 10em;
    padding: 0.5em;
    font-weight: bold;
}

.play-again:hover {
    background-color: rgb(41, 41, 41);
    border: solid white;
    color: white;
}

.match {
    animation-name: shake;
    animation-duration: 300ms;
}

@keyframes shake {
    0% {
        transform: translateX(0)
    }

    25% {
        transform: translateX(5px)
    }

    50% {
        transform: translateX(-5px)
    }

    75% {
        transform: translateX(5px)
    }

    100% {
        transform: translateX(0)
    }
}

@media screen and (max-width: 700px) {

    .score-tag,
    .score {
        font-size: 1.1rem;   
    }
    .title-container {
        display: none;
    }
    .score-container {
        margin-left: 0.5em;
        padding: 1em;
    }
}